import './styles/styles.css';
import logoImage from './assets/logo.png';
import Task from './tasksComponent';
import inBox from './InBox';
import addData, { displayTasks } from './addData';
import { cancelForm } from './remove';

const logo = document.querySelector('.logo');
logo.src = logoImage;

const tasksContainer = document.getElementById('tasksContainer');
const addTask = document.querySelector('.addTaskBtn');

//form data
const form = document.querySelector('form');
const title = document.getElementById('title');
const description = document.getElementById('description');
const date = document.getElementById('date');
const priority = document.getElementById('priority');
const taskBtn = document.getElementById('submit');
const cancelBtn = document.getElementById('cancel');

const inBoxContainer = inBox().inBox;
tasksContainer.insertBefore(inBoxContainer, addTask);
const dataBase = inBox().inBoxData;

addTask.addEventListener('click', () => {
  form.style.display = 'block';
  addTask.style.display = 'none';
});

if (localStorage.getItem('taskDatabase')) {
  const tasks = JSON.parse(localStorage.getItem('taskDatabase'));
  tasks.forEach((task) => {
    task = addData(task);

    //display task on page
    inBoxContainer.appendChild(displayTasks(task));
  });
}

taskBtn.addEventListener('click', function () {
  const task = new Task(
    title.value,
    description.value,
    date.value,
    priority.value
  );

  //store task in database in html format
  dataBase.push(task);
  const taskElement = addData(task);
  localStorage.setItem('taskDatabase', JSON.stringify(dataBase));

  //display task on page
  inBoxContainer.appendChild(displayTasks(taskElement));
  form.style.display = 'none';
  addTask.style.display = 'block';

  //clear inputs
  document.querySelectorAll('form input').forEach((input) => {
    input.value = '';
  });
});

//remove form
cancelForm(cancelBtn, form, addTask);
